*,
:after,
:before {
  box-sizing: inherit;
}

html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}
body {
  line-height: 1;
}
ol,
ul {
  list-style: none;
}
blockquote,
q {
  quotes: none;
}
blockquote:before,
blockquote:after,
q:before,
q:after {
  content: '';
  content: none;
}
table {
  border-collapse: collapse;
  border-spacing: 0;
}

body {
  color: white;
}

a {
  color: #fff;
  text-transform: uppercase;
  text-decoration: none;
  letter-spacing: 0.15em;
  display: inline-block;
  padding: 15px 20px;
  position: relative;
}

a:not(.logo-link):after {
  background: none repeat scroll 0 0 transparent;
  bottom: 0;
  content: '';
  display: block;
  height: 2px;
  left: 50%;
  position: absolute;
  background: #fff;
  transition: width 0.3s ease 0s, left 0.3s ease 0s;
  width: 0;
}
a:not(.logo-link):hover:after {
  width: 100%;
  left: 0;
}

li {
  list-style: none;
}

.App {
  text-align: center;
  background-size: cover;
  min-height: 100vh;
}

.nav-links {
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 20vh;
  margin-right: auto;
  padding: 0 5rem;
  color: white;
  list-style: none;
  font-size: 1.2rem;
  font-weight: 500;
  font-family: 'Aldrich', sans-serif;
}

.logo-link {
  display: flex;
  justify-content: center;
  align-items: center;
  letter-spacing: 0.1rem;
  font-style: italic;
}

.logo-link > img {
  height: 50px;
  width: 35px;
  margin-right: 1rem;
}

.nav-menu {
  display: flex;
  align-items: center;
  gap: 12rem;
  margin-left: auto;
  text-transform: uppercase;
}

.shopping-cart {
  height: 50px;
  width: 40px;
  outline: none;
  background: black;
  border: 1px solid white;
  border-radius: 50%;
  font-size: 1.5rem;
  cursor: pointer;
  color: white;
}
